/* 0. Reset ================================= */
@import 'reset';


/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}

body {
	background: #ccc;
}




/* 2. Content ================================= */


.wrapper{
	display: flex;
}
/* Always set the map height explicitly to define the size of the div
* element that contains the map. */
#map {
width: 50%;
}

#map, .wrapper {
height: 100%;
}
#results{
  width:50%;
  padding: 2rem;
  overflow: scroll;
}
/* Optional: Makes the sample page fill the window. */
html, body {
height: 100%;
margin: 0;
padding: 0;
}

.flickr_result{
  display: flex;
  flex-wrap: wrap;
	.flickr_pane{
		width: 200px;
		height: 200px;
	}
}

